import { Link } from "react-router-dom";
import Carousel from "../../components/Carousel";
import { useFeaturedBrands } from "../../services/brandService";
import useMediaQuery from "../../hooks/useMediaQuery";

const FeaturedBrands = () => {
  const { data: featuredBrands = [], isLoading, error } = useFeaturedBrands();
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  if (isLoading) {
    return <></>;
  }

  if (error) {
    return <div>{error.message}</div>;
  }

  return (
    featuredBrands?.length > 0 && (
      <div className="container max-w-full space-y-6 max-lg:space-y-4">
        <h1 className="text-center text-2xl max-lg:text-xl font-bold font-secondary">
          Explore Top Brands
        </h1>

        <div className="max-w-max mx-auto">
          <Carousel
            slideWidth={maxLg ? 60 : maxXl ? 80 : 120}
            allowTouchMove
            spaceBetween={maxLg ? 10 : 30}
          >
            {featuredBrands.map((brand) => (
              <Link
                to={`/brand/${brand._id}`}
                key={brand.name || brand._id}
                className="inline-block relative"
              >
                <img
                  src={brand.logoUrl}
                  alt={brand.name || brand._id}
                  loading="lazy"
                  className="size-[100px] max-xl:size-[80px] max-lg:size-[60px] object-cover object-center"
                />
              </Link>
            ))}
          </Carousel>
        </div>
      </div>
    )
  );
};

export default FeaturedBrands;
