import { useEffect, useRef, useState } from "react";
import useMediaQuery from "../hooks/useMediaQuery";
import { useBanners } from "../services/bannerServices";
import Carousel from "./Carousel";
import Skeleton from "./Skeleton";
import useInView from "../hooks/useInView";

const WideSkyscraper = ({
  preload = false,
  position,
  resourceType,
  valueId,
  title,
  subtitle,
  className,
  containerClassName = "container max-w-full",
}) => {
  const containerRef = useRef(null);
  const hasFetchedOnce = useRef(false);
  const [shouldFetch, setShouldFetch] = useState(preload);
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");
  const maxMd = useMediaQuery("(max-width: 768px)");

  const isInView = useInView(containerRef, {
    threshold: 0.1,
    rootMargin: "100px",
  });

  const { data: banners = [], isLoading } = useBanners(
    {
      bannerType: "Wide Skyscraper",
      position,
      resourceType,
      valueId,
    },
    { enabled: shouldFetch }
  );

  useEffect(() => {
    if (!hasFetchedOnce.current && isInView && position && resourceType) {
      setShouldFetch(true);
      hasFetchedOnce.current = true;
    }
  }, [isInView, position, resourceType]);

  return (
    <div ref={containerRef}>
      {isLoading ? (
        <ul className={`flex gap-4 overflow-x-hidden ${containerClassName}`}>
          {Array.from({ length: 10 }).map((_, index) => (
            <li
              key={index}
              className={`w-[250px] max-xl:w-[200px] max-lg:w-[150px] h-[500px] max-xl:h-[400px] max-lg:h-[300px] rounded-lg overflow-hidden flex-shrink-0 ${className}`}
            >
              <Skeleton overall="light" />
            </li>
          ))}
        </ul>
      ) : (
        banners?.length > 0 && (
          <div
            className={`space-y-4 max-lg:space-y-2 max-md:pr-0 ${containerClassName}`}
          >
            {title || subtitle ? (
              <div>
                <h1 className="text-2xl max-lg:text-xl font-bold">{title}</h1>
                <p className="text-sm">{subtitle}</p>
              </div>
            ) : null}
            <Carousel
              slideWidth={maxLg ? 150 : maxXl ? 200 : 250}
              navigation={!maxMd}
              autoplay
              delay={5000}
              spaceBetween={maxLg ? 10 : 14}
              allowTouchMove
            >
              {banners.map((banner) => (
                <a
                  key={banner.bannerTitle || banner._id}
                  href={banner.linkUrl}
                  className="w-max inline-block space-y-1"
                >
                  <img
                    src={banner.bannerImg}
                    alt={banner.title || banner._id}
                    loading="lazy"
                    className={`w-[250px] max-xl:w-[200px] max-lg:w-[150px] h-[500px] max-xl:h-[400px] max-lg:h-[300px] object-cover object-center rounded-lg ${className}`}
                  />
                </a>
              ))}
            </Carousel>
          </div>
        )
      )}
    </div>
  );
};

export default WideSkyscraper;
