import { useEffect, useRef, useState } from "react";
import { useBanners } from "../services/bannerServices";
import Skeleton from "./Skeleton";
import useInView from "../hooks/useInView";

const Tinybar = ({
  preload = false,
  position,
  resourceType,
  valueId,
  title,
  subtitle,
  className,
  containerClassName = "container max-w-full",
}) => {
  const containerRef = useRef(null);
  const hasFetchedOnce = useRef(false);
  const [shouldFetch, setShouldFetch] = useState(preload);

  const isInView = useInView(containerRef, {
    threshold: 0.1,
    rootMargin: "100px",
  });

  const { data: banners = [], isLoading } = useBanners(
    {
      bannerType: "Tinybar",
      position,
      resourceType,
      valueId,
    },
    { enabled: shouldFetch }
  );

  useEffect(() => {
    if (!hasFetchedOnce.current && isInView && position && resourceType) {
      setShouldFetch(true);
      hasFetchedOnce.current = true;
    }
  }, [isInView, position, resourceType]);

  return (
    <div ref={containerRef}>
      {isLoading ? (
        <ul
          className={`flex max-md:flex-wrap justify-center gap-4 max-lg:gap-2 overflow-x-hidden ${containerClassName}`}
        >
          {Array.from({ length: 2 }).map((_, index) => (
            <li
              key={index}
              className={`inline-block w-full h-16 max-md:h-10 overflow-hidden flex-shrink-0 ${className}`}
            >
              <Skeleton />
            </li>
          ))}
        </ul>
      ) : (
        banners?.length > 0 && (
          <div className={`space-y-6 max-lg:space-y-2 ${containerClassName}`}>
            {title || subtitle ? (
              <div className="border-b-2 border-secondary pb-2 leading-none">
                <h1 className="text-2xl max-lg:text-xl font-bold font-secondary">
                  {title}
                </h1>
                <p>{subtitle}</p>
              </div>
            ) : null}

            <div className="flex max-md:flex-wrap justify-center gap-4 max-lg:gap-2">
              {banners.map((banner) => (
                <a
                  key={banner.bannerTitle || banner._id}
                  href={banner.linkUrl}
                  className="inline-block w-full h-max"
                >
                  <img
                    src={banner.bannerImg}
                    alt={banner.bannerTitle || banner._id}
                    loading="lazy"
                    className={`object-cover object-center ${className}`}
                  />
                </a>
              ))}
            </div>
          </div>
        )
      )}
    </div>
  );
};

export default Tinybar;
