import { Link } from "react-router-dom";
import Carousel from "../../components/Carousel";
import useMediaQuery from "../../hooks/useMediaQuery";
import brandBg from "../../assets/images/Brand bg.png";
import { useTopSellingBrands } from "../../services/brandService";

const PopularBrands = () => {
  const { data, isLoading } = useTopSellingBrands();
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  return (
    !isLoading &&
    data?.length > 0 && (
      <div className="container max-w-full space-y-6 max-lg:space-y-4">
        <div className="max-w-max mx-auto">
          <Carousel
            slideWidth={maxLg ? 80 : maxXl ? 100 : 120}
            allowTouchMove
            spaceBetween={maxLg ? 0 : 10}
          >
            {data?.map((info, index) => (
              <Link
                to={`/brand/${info?.brand._id}`}
                key={info?.brand.name}
                className="flex justify-center items-center w-[120px] max-xl:w-[100px] max-lg:w-[80px] h-[90px] max-xl:h-[75px] max-lg:h-[60px]"
              >
                <div
                  className={`relative size-4/5 flex items-center justify-center shadow-pop overflow-hidden ${
                    index % 2 === 0 ? "rounded-lg" : "rounded-full"
                  }`}
                >
                  <img
                    src={brandBg}
                    alt={info?.brand.name}
                    loading="lazy"
                    className="absolute inset-0 object-cover object-center z-0"
                  />
                  <img
                    src={info?.brand.logoUrl}
                    alt={info?.brand.name}
                    loading="lazy"
                    className={`absolute size-4/5 object-cover object-center z-[1] ${
                      index % 2 === 0 ? "rounded-lg" : "rounded-full"
                    }`}
                  />
                </div>
              </Link>
            ))}
          </Carousel>
        </div>
      </div>
    )
  );
};

export default PopularBrands;
