import FeaturedBrands from "./FeaturedBrands";
import CategorySlide from "./CategorySlide";
import DealOfTheDay from "../../components/DealOfTheDay";
import FeaturedDeals from "../../components/FeaturedDeals";
import FlashDeals from "../../components/FlashDeals";
import HeroBanner from "../../components/HeroBanner";
import LeaderBoard from "../../components/LeaderBoard";
import FeaturedProducts from "./FeaturedProducts";
import RecentlyViewed from "../../components/RecentlyViewed";
import SquareBanner from "../../components/SquareBanners";
import SubcategorySlide from "./SubcategorySlide";
import SmallRectangle from "../../components/SmallRectangle";
import LargeRectangle from "../../components/LargeRectangle";
import NewArrivals from "./NewArrivals";
import InfiniteText from "../../components/InfiniteText";
import bestOfFinafidBg from "../../assets/images/Best of Finafid.png";
import PopularBrands from "./PopularBrands";
import PopularProducts from "./PopularProducts";
import FeaturedProductTypes from "./FeaturedProductTypes";
import WideSkyscraper from "../../components/WideSkyscraper";
import Tinybar from "../../components/Tinybar";
import Microbar from "../../components/Microbar";

const Home = () => {
  return (
    <section className="lg:space-y-8 space-y-4 lg:py-8 py-4">
      <SmallRectangle position="Top" resourceType="Home" preload />
      <CategorySlide preload />
      <LeaderBoard
        position="Middle_1"
        resourceType="Home"
        containerClassName=""
        preload
      />
      <RecentlyViewed />
      <WideSkyscraper position="Middle_1" resourceType="Home" />
      <DealOfTheDay />
      <FeaturedDeals />
      <FeaturedBrands />
      <FlashDeals />
      <SubcategorySlide />
      <Tinybar
        position="Middle_2"
        resourceType="Home"
        title="Sale"
        subtitle="Only for you"
      />
      <LeaderBoard
        position="Middle_2"
        resourceType="Home"
        containerClassName=""
      />
      <SquareBanner position="Middle_2" resourceType="Home" />
      <FeaturedProducts />
      <HeroBanner
        position="Middle_2"
        resourceType="Home"
        containerClassName=""
      />
      <LargeRectangle position="Middle_2" resourceType="Home" />
      <NewArrivals />
      <HeroBanner
        position="Middle_3"
        resourceType="Home"
        containerClassName=""
      />
      <div className="container max-w-full py-2 bg-whitePink text-hotPink">
        <InfiniteText
          slides={Array.from({ length: 20 }).map(() => "sale")}
          duration={0}
        />
      </div>
      <FeaturedProductTypes />
      <div className="w-full relative">
        <img
          src={bestOfFinafidBg}
          alt="Best of Finafid"
          className="w-full h-max object-cover object-center"
        />
        <h2 className="absolute top-1/2 -translate-y-1/2 right-1/2 translate-x-1/2 text-xl max-lg:text-lg max-md:text-base max-sm:text-sm font-semibold">
          Best at FINAFID
        </h2>
      </div>
      <PopularBrands />
      <PopularProducts />
      <HeroBanner
        position="Middle_4"
        resourceType="Home"
        containerClassName=""
      />
      <Tinybar position="Middle_4" resourceType="Home" />
      <Microbar position="Middle_4" resourceType="Home" containerClassName="" />
      <LargeRectangle position="Middle_4" resourceType="Home" />
    </section>
  );
};

export default Home;
