import { Link } from "react-router-dom";
import Carousel from "../../components/Carousel";
import useMediaQuery from "../../hooks/useMediaQuery";
import { useEffect, useRef, useState } from "react";
import useInView from "../../hooks/useInView";
import { useAllFeaturedCategories } from "../../services/categoryServices";
import Skeleton from "../../components/Skeleton";

const CategorySlide = ({ preload = false }) => {
  const containerRef = useRef(null);
  const hasFetchedOnce = useRef(false);
  const [shouldFetch, setShouldFetch] = useState(preload);
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  const isInView = useInView(containerRef, {
    threshold: 0.1,
    rootMargin: "100px",
  });

  const { data: featuredCategories = [], isLoading } = useAllFeaturedCategories(
    {
      enabled: shouldFetch,
    }
  );

  useEffect(() => {
    if (!hasFetchedOnce.current && isInView) {
      setShouldFetch(true);
      hasFetchedOnce.current = true;
    }
  }, [isInView]);

  return (
    <div ref={containerRef}>
      {isLoading ? (
        <ul className="container max-w-full flex justify-center items-center gap-4 overflow-x-hidden">
          {Array.from({ length: 7 }).map((_, index) => (
            <li
              key={index}
              className="size-[120px] max-xl:size-[100px] max-lg:size-[80px] rounded-full overflow-hidden flex-shrink-0"
            >
              <Skeleton />
            </li>
          ))}
        </ul>
      ) : (
        featuredCategories?.length > 0 && (
          <div className="container max-w-full space-y-6 max-lg:space-y-4">
            <h1 className="text-center text-2xl max-lg:text-xl font-bold font-secondary">
              Shop by Category
            </h1>

            <div className="max-w-max mx-auto">
              <Carousel
                slideWidth={maxLg ? 80 : maxXl ? 100 : 120}
                spaceBetween={maxLg ? 10 : 30}
                allowTouchMove
              >
                {featuredCategories.map((category) => (
                  <Link
                    to={`/category/${category._id}`}
                    key={category._id}
                    className="flex justify-center items-center relative rounded-2xl overflow-hidden"
                  >
                    <img
                      src={category.logoUrl}
                      alt={category.name || category._id}
                      loading="lazy"
                      className="size-[120px] max-xl:size-[100px] max-lg:size-[80px] object-cover object-center rounded-full"
                    />
                  </Link>
                ))}
              </Carousel>
            </div>
          </div>
        )
      )}
    </div>
  );
};

export default CategorySlide;
