import { Link } from "react-router-dom";
import Carousel from "../../components/Carousel";
import useMediaQuery from "../../hooks/useMediaQuery";
import { useAllFeaturedProductTypes } from "../../services/categoryServices";

const FeaturedProductTypes = () => {
  const { data: featuredProductTypes, isLoading } =
    useAllFeaturedProductTypes();
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxLg = useMediaQuery("(max-width: 1024px)");

  return (
    !isLoading &&
    featuredProductTypes?.length > 0 && (
      <div className="container max-w-full">
        <Carousel
          slideWidth={maxLg ? 100 : maxXl ? 200 : 300}
          navigation
          autoplay
          delay={5000}
          spaceBetween={maxLg ? 10 : 14}
          allowTouchMove
        >
          {featuredProductTypes.map((productType) => (
            <Link
              key={productType._id}
              to={`/product-type/${productType._id}`}
              className="w-max inline-block space-y-1"
            >
              <img
                src={productType.logoUrl}
                alt={productType.name || productType._id}
                loading="lazy"
                className="size-[300px] max-xl:size-[200px] max-lg:size-[100px] object-cover object-center rounded-lg"
              />
              {/* <h3 className="text-sm max-xl:text-xs max-lg:text-xxs font-medium">
                {productType.name}
              </h3> */}
            </Link>
          ))}
        </Carousel>
      </div>
    )
  );
};

export default FeaturedProductTypes;
