import { Link } from "react-router-dom";
import Carousel from "./Carousel";
import useMediaQuery from "../hooks/useMediaQuery";
import { useFlashDeals } from "../services/dealServices";
import { useEffect, useRef, useState } from "react";
import useInView from "../hooks/useInView";
import Skeleton from "./Skeleton";

const FlashDeals = ({ preload = false }) => {
  const containerRef = useRef(null);
  const hasFetchedOnce = useRef(false);
  const [shouldFetch, setShouldFetch] = useState(preload);
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const maxMd = useMediaQuery("(max-width: 768px)");

  const isInView = useInView(containerRef, {
    threshold: 0.1,
    rootMargin: "100px",
  });

  const { data: deals = [], isLoading } = useFlashDeals({
    enabled: shouldFetch,
  });

  useEffect(() => {
    if (!hasFetchedOnce.current && isInView) {
      setShouldFetch(true);
      hasFetchedOnce.current = true;
    }
  }, [isInView]);

  return (
    <div ref={containerRef}>
      {isLoading ? (
        <ul className="container max-w-full flex gap-4 overflow-x-hidden">
          {Array.from({ length: 10 }).map((_, index) => (
            <li
              key={index}
              className="w-[210px] max-xl:w-[180px] h-[280px] max-xl:h-[240px] rounded-lg  overflow-hidden flex-shrink-0"
            >
              <Skeleton />
            </li>
          ))}
        </ul>
      ) : (
        deals.length > 0 && (
          <div className="container max-w-full space-y-8 max-lg:space-y-4 max-md:pr-0">
            <h1 className="text-2xl max-lg:text-xl font-bold font-secondary">
              Flash Deals
            </h1>

            <Carousel
              slideWidth={maxXl ? 180 : 210}
              navigation={!maxMd}
              autoplay
              spaceBetween={maxXl ? 10 : 14}
              delay={5000}
              allowTouchMove
            >
              {deals.map((deal) => (
                <Link
                  key={deal.title || deal._id}
                  to={`/flash-deal/${deal._id}`}
                  className="block relative"
                >
                  <img
                    src={deal.banner}
                    alt={deal.title || deal._id}
                    loading="lazy"
                    className="w-[210px] max-xl:w-[180px] h-[280px] max-xl:h-[240px] rounded-lg object-cover object-center"
                  />
                  <h3 className="py-2 text-lg max-xl:text-sm font-medium font-secondary">
                    {deal.title}
                  </h3>
                </Link>
              ))}
            </Carousel>
          </div>
        )
      )}
    </div>
  );
};

export default FlashDeals;
